"use client"

import React from 'react';

export default function Home() {
  React.useEffect(() => {
    const box2 = document.querySelector('.box2');
    const updateDimensions = () => {
      document.getElementById('box2-width').innerText = box2.offsetWidth;
      document.getElementById('box2-height').innerText = box2.offsetHeight;
    };
    updateDimensions();
    window.addEventListener('resize', updateDimensions);
    return () => window.removeEventListener('resize', updateDimensions);
  }, []);
  return (
    <div className="container">
      <div className="box box1">box1</div>
      <div className="box box2">box2 
      <span className="box-dimensions">Width: <span id="box2-width"></span>px, Height: <span id="box2-height"></span>px</span>
      </div>
      <div className="box box3">box3</div>
      <div className="box box4">box4</div>
      <div className="box box5">box5</div>
      <div className="box box6">box6</div>
      <div className="box box7">box7</div>
      <div className="box box8">box8</div>
    </div>
  );
}
